'use strict'

import React from 'react';
import {Link} from 'react-router';

export default class Bar extends React.Component {
    render() {
        return (
            <div className='btn-group btn-group-justified btn-group-raised'>
                <Link dataToggle='modal' dataTarget='#post' className='btn' id='custom-btn'>發文</Link>
                <Link to='/pdfUpload' className='btn' id='custom-btn'>新增題目</Link>
                <Link dataToggle='modal' dataTarget='#upload' className='btn' id='custom-btn'>自訂考卷</Link>
            </div>
        );
    }
}
