'use strict'

import React from 'react';
import Article from './Article';
import Bar from './Bar';
import {Link} from 'react-router';

export default class Home extends React.Component {
  render() {
      const displayNone = {
          display: 'none'
      }
    return (
        <section id='main'>
            <div className='container'>
                <div className='row'>
                <div className='col-md-8 col-md-offset-2'>
                    <Bar></Bar>
                </div>
                    <div className='col-md-8 col-md-offset-2'>
                        <Article></Article>
                    </div>
                </div>
            </div>
            <div className='modal fade' id='post' tabindex='-1' role='dialog' ariaLabelledby='myModalLabel'>
                <div className='modal-dialog' role='document'>
                    <div className='modal-content'>
                        <form method='post' action='/post'>
                            <div className='modal-header'>
                                <button type='button' className='close' data-dismiss='modal' ariaLabel='Close'><span ariaHidden='true'>&times;</span></button>
                                <h4 className='modal-title' id='myModalLabel'>Write post</h4>
                            </div>
                            <div className='modal-body'>
                                <div className='form-group label-floating'>
                                    <label className='control-label'>Title</label>
                                    <input type='text' name='title' className='form-control' autoComplete='off'></input>
                                </div>
                                <div className='form-group'>
                                    <textarea type='text' name='editor1' className='form-control' placeholder='Page Body' rows = '20' cols = '100'></textarea>
                                </div>
                                <div className='form-group'>
                                    <label>Tags</label>
                                    <input type='text' name='tags' className='form-control' placeholder='Use # to add tags' autoComplete='off'></input>
                                </div>
                                <div className='form-group'>
                                    <input type='text' name='fileName' id='fileName' style={displayNone}></input>
                                    <p>附件</p>
                                    <div id='here'>
                                        <p id='title'></p>
                                        <div id='progressBar' style={displayNone}>
                                            <div className='bs-component'>
                                                <div className='progress progress-striped active'>
                                                    <div className='progress-bar' id='percentage'></div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div id='fileSelect'>
                                        <input type='file' name='file' id='fileElem1' className='form-control' onchange='handleFiles1(this.files)' style={displayNone}></input>
                                        <Link to='#' id='fileSelect1' className='btn'>+</Link>
                                    </div>
                                </div>
                            </div>
                            <div className='modal-footer'>
                                <button type='button' className='btn btn-default' dataDismiss='modal'>Cancel</button>
                                <button type='submit' className='btn btn-primary'>Post</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            <div className='modal fade' id='upload' tabindex='-1' role='dialog' aria-labelledby='myModalLabel'>
                <div className='modal-dialog' role='document'>
                    <div className='modal-content'>
                            <div className='modal-header'>
                                <Link to='#' type='button' className='close' data-dismiss='modal' aria-label='Close' id='close' style={displayNone}><span ariaHidden='true'>&times;</span></Link>
                            </div>
                            <div className='modal-body' id='area'>
                                <h3 id='title'>自訂考卷</h3>
                                <div id='progressBar' style={displayNone}>
                                    <div className='bs-component'>
                                        <div className='progress progress-striped active'>
                                            <div className='progress-bar' id='percentage'></div>
                                        </div>
                                    </div>
                                </div>
                                <form method='post' action='/newtest'>
                                <div id='fileSelect'>
                                    <input type='text' name='title' className='form-control' autoComplete='off' placeholder='考卷名稱'></input>
                                </div>
                                <div className='form-group'>
                                    <label>選擇科目</label>
                                    <div className='radio radio-primary'>
                                        <label>
                                        <input type='radio' name='subject' value='chinese' id='radio1'></input>
                                        國文
                                        </label>
                                    </div>
                                    <div className='radio radio-primary'>
                                        <label>
                                        <input type='radio' name='subject' value='english' id='radio2'></input>
                                        英文
                                        </label>
                                    </div>
                                    <div className='radio radio-primary'>
                                        <label>
                                        <input type='radio' name='subject' value='math' id='radio3'></input>
                                        數學
                                        </label>
                                    </div>
                                    <div className='radio radio-primary'>
                                        <label>
                                        <input type='radio' name='subject' value='social'></input>
                                        社會
                                        </label>
                                    </div>
                                    <div className='radio radio-primary'>
                                        <label>
                                        <input type='radio' name='subject' value='science'></input>
                                        自然
                                        </label>
                                    </div>
                                    <div className='modal-footer'>
                                        <button type='submit' className='btn btn-primary'>新增</button>
                                    </div>
                                </div>
                                </form>
                            </div>
                            <div className='modal-footer'>
                                <hr></hr>
                            </div>
                    </div>
                </div>
            </div>
        </section>
    );
  }
}
